import React, {Component} from 'react';
import Sectiontitle from '../component/Banner/Sectiontitle';
import Fade from 'react-reveal/Fade';
import { Container } from 'react-bootstrap';
import '../assets/css/service.css'

class Service extends Component{
    render(){
        let jhonData = this.props.jhonData;
        var {wClass} = this.props
        return(
            <section className={`${wClass}`} id="service">
                <div className="container">
                    <Sectiontitle Title="Why should you vote for me?"></Sectiontitle> <p style={{"text-align":"center"}}>The CSESoc community has been huge part of uni life when it comes to making friends aswell as
                    course help. As a director in CSESoc, it's made me very appreciative of how much this society has to offer and realise the potential that it has. I'd love the
                    opportunity to be an exec next year to continue building on the foundations set by previous teams and unlock more of CSESoc's potential!</p>
                    <br></br>
                    <Fade bottom cascade duration={1000}>
                        <div className="row">
                            {
                                jhonData.service && jhonData.service.map(item =>{
                                return(
                                        <div className="col-lg-4 col-sm-6" key={item.id}>
                                            <div className="work_item wow fadeInUp" data-wow-delay="0.1s">
                                                <i className={item.iconName}></i>
                                                <a href=".#"><h2 className="t_color">{item.serviceTitle}</h2></a>
                                                <Container fluid>{item.sDetails}</Container>
                                            </div>
                                        </div>
                                        )
                                    }
                                )
                            }
                        </div>
                    </Fade>
                </div>
            </section>
        )
    }
}

export default Service;