import React, {Component} from 'react';
import Isotope from 'isotope-layout/js/isotope';
import ImagesLoaded from 'imagesloaded/imagesloaded';
import Image from 'react-bootstrap/Image';


class ItemGrid extends Component {

    state = {
        activeItem: '*',
    }

    newTab(url) {
        window.open(url);
    }

    componentDidMount() {
        var imgLoad = new ImagesLoaded('.grid');
    
        imgLoad.on('progress', function(instance, image) {
            this.iso = new Isotope('.grid', {
                itemSelector: '.grid-item',
                layoutMode: "masonry"
            });
        }); 
        
    }
    onFilterChange = (newFilter) => {
        
        this.setState({activeItem: newFilter});
        if (this.iso === undefined) {
            this.iso = new Isotope('.grid', {
            itemSelector: '.grid-item',
            layoutMode: "masonry"
            });
        }
    
    // this.iso.arrange({ filter: newFilter });
        
      if(newFilter === '*') {
        this.iso.arrange({ filter: `*` });
      } else {
        this.iso.arrange({ filter: `.${newFilter}` });
      }
    }

    onActive = v => v === this.state.activeItem ? 'active' : '';
    render() {
        return(
            <div>
                <div className="grid row">
                    <div className="col-md-6 col-sm-6 col-xs-12 grid-item tech web">
                        <div className="portfolio hover-style">
                            <Image width="560" height="315" src={require('../../image/firstYearGuide_Banner.png')} alt="Firt year Guide"></Image>
                                <div className="item-img-overlay" onClick={()=>this.newTab("https://media.csesoc.org.au/first-year-guide/")}>
                                    <div className="overlay-info text-center">
                                        <h6 className="sm-titl">CSESoc First Year Guide 2022</h6>
                                    <div className="icons">
                                        <p>Click to read</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 col-sm-6 col-xs-12 grid-item market">
                        <div className="portfolio hover-style">
                            <Image width="100%" height="315" src={require('../../image/mindfulness-banner-01.png')}></Image>
                                <div className="item-img-overlay" onClick={()=>this.newTab("https://media.csesoc.org.au/meditation-and-mindfulness/")}>
                                    <div className="overlay-info text-center">
                                        <h6 className="sm-titl">Mindfulness and Meditation by Raman Singh</h6>
                                    <div className="icons">
                                        <p>Click to read</p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 col-sm-6 col-xs-12 grid-item tech web">
                        <div className="portfolio hover-style">
                            <iframe width="540" height="315" src="https://www.youtube.com/embed/HeldH4AvkRg" title="YouTube video player" frameborder="0" 
                            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
                        </div>
                    </div>
                    <div className="col-md-6 col-sm-6 col-xs-12 grid-item tech web">
                        <iframe width="540" height="315" src="https://www.youtube.com/embed/7RzOuX8Kbx0" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>				
                    </div>
                </div>
            </div>
        )
    }
  }

  export default ItemGrid;
