import React, {Component} from 'react';
import ItemGrid from './ItemGrid';
import Sectiontitle from '../../component/Banner/Sectiontitle';
import Image from 'react-bootstrap/Image';
import { Container, Col, Row } from 'react-bootstrap';
import '../../assets/css/portfolio.css';

class portfolio extends Component {
    render() {
      var {pClass}= this.props;
      return(
        <section className={`portfolio_area ${pClass}`} id="portfolio">
            <div className="container">
                <Sectiontitle Title="Contributions"/>
                <p>Over the past year, I've had the privelidge of overseeing the videos, podcasts and articles produced by CSESoc. 
                  This year in the media portfolio we placed emphasis on producing content with focus on inclusivity and diversity. With my wonderful
                  co-directors Amanda and Caleb aswell as our amazing subcom, here is some of the content our portfolio produced this year.</p>
                  <br></br>
                <ItemGrid/>
                <br></br>
                <Container>
                  <Row>
                  <Col>
                    <Image fluid src={require('../../image/collage1.png')}></Image>
                  </Col>
                  <Col>
                    <div>
                      <p style={{textAlign:"left"}}>
                        Besides my role as a director, I've been highly involved with CSESoc during my
                        two years of university. This includes:
                      </p>
                      <ul>
                        <li>Welcome Week Stall Volunteer [2022]</li>
                        <li>Studex Peer Mentor [2022]</li>
                        <li>First Year Camp Leader [2022]</li>
                        <li>Media Subcommittee [2021]</li>
                        <li>First Year Campee [2021]</li>
                      </ul>
                    </div>
                  </Col>
                  </Row>
                </Container>
            </div>
        </section>
      )
    }
  }

  export default portfolio;
